@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100..1000&display=swap');
$pFont: "Roboto Flex", sans-serif;
$mFont: 'Bebas Neue', monospace;
$backgroundColor: #1B1B1B;
$c1: #9fef00;

.mNAbout {z-index: 2;}
.mNServices {z-index: 3;}
.mNTeams {z-index: 4;}
.mNContact {z-index: 5;}
.address {
    display: none !important; //here
}
.thisDesktop, .thisDesktop2, .thisDesktop3 {
    display: none !important;
}
::selection {
    background-color: #9fef00;
    color: #1B1B1B;
}
.h1, .faded {
    opacity: 0;
}
.h1 {
    transform: translateY(20px);
}
.tabAlert {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    background: #1B1B1B;
    width: 100%;
    height: 100%;
    opacity: 0;
}
.loader-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 998;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #1B1B1B;
    img {
        width: 70%;
        opacity: 0;
    }
}
.loader-hidden {
    transform: translateY(-100%);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background: #1B1B1B;
    font-weight: 400;
    color: #f8f8f8;
    font-family: $mFont;
    overflow: hidden;
}
button {
    background: transparent;
    border: 0;
}
a {
    text-decoration: none;
    color: #f8f8f8;
    cursor: pointer;
    font-family: $mFont;
}
p {
    font-family: $pFont;
}
i, span {
    color: #9fef00;
}

ul {
    list-style: none;
}

img {
    width: 20px;
}
.actBtn {
    background: rgba(0, 0, 0, 0);
    border: 1px solid #f8f8f8;
    position: relative;
    cursor: pointer;
    transition: .3s;
    a {
      width: 100%;
      height: 100%;
      &:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: calc(100% + 3px);
        border-right: 1px solid #f8f8f8;
        border-bottom: 1px solid #f8f8f8;
        display: block;
        z-index: -1;
        transition: .5s;
      }
    }
      &:hover {
        background: rgba(159, 239, 0, 1);
        border: 1px solid #9fef00;
        a {
            color: #1B1B1B;
          &:before {
            transform: translate(0px, 0px);
            height: 100%;
            border-right: 1px solid transparent;
            border-bottom: 1px solid transparent;
          }
        }
      }
  }
.tStroke {
    color: transparent !important; /* Atur warna teks transparan */
    -webkit-text-stroke-width: .8px; /* Atur lebar outline */
    -webkit-text-stroke-color: #f8f8f8; /* Atur warna outline */
    -webkit-text-fill-color: transparent;
    stroke-width: .8px;
  }
  .tShadow {
    width: 100%;
    position: relative;
      &:before {
        content: 'Wild Currents';
        position: absolute;
        color: #000;
        width: 100%;
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: rgb(0, 0, 0);
        -webkit-text-fill-color: #000;
        z-index: -1;
      }
  }
.btH {
    text-align: center;
    display: none;
}

.mainContainer {
    width: 100%;
    height: 100vh;
    display: flex;
}
.mainNav {
    width: 100%;
    // position: relative;
    display: flex;
}
.mainNavB {
    width: 100%;
    position: relative;
    display: flex;
}
.mainContent {
    height: 100%;
    // flex: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}
.mNContainer {
    background: #9fef00;
    border: 1px solid #f8f8f8;
    border-bottom: 0;
    flex-grow: 1;
    position: relative;
    i, a {
        color: #1B1B1B;
    }
    a {
        display: flex;
        align-items: center;
        height: 100%;
        width: 100%;
        transition: .3s;
        &:hover {
            background: #f8f8f8;
        }
        div {
            width: 100%;
        }
    }
}
.mNCtxt {
    text-align: right;
}
.mNContact {
    border-bottom: 1px solid #f8f8f8 !important;
}

/* MAIN CONTENT HOME */
.mCtop {
    display: flex;
    width: 100%;
}
.mCtopL {
    display: flex;
}
.mCtopR {
    display: flex;
}
.mCtopRmail {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    span {
        color: #f8f8f8;
    }
    a {
        display: flex;
        border: 1px solid #f8f8f8;
        &:hover {
            .mailIco {
                background: #9fef00;
            }
            i {
                color: #1B1B1B;
            }
        }
    }
    .mailIco {
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .3s;
    }
    .mailTxt {
        border-left: 1px solid #f8f8f8;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.mCmid {
    flex: 1;
    display: flex;
    position: relative;
}
.mCmidHero {
    position: absolute;
}
.heroHeader {
    display: flex;
}
.mCmidImg {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}
/* MAIN CONTENT HOME */

/* MAIN CONTENT ABOUT */
.showMobile {
    display: none;
}
.mCaBcontainer {
    display: flex;
    flex-direction: column;
}
/* MAIN CONTENT ABOUT */

/* MAIN CONTENT SERVICES */
.mCservicesBot {
    flex: 1;
}
.mcsbPack {
    display: flex !important;
    position: relative;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
    }
}
.mcsbPackI {
    display: flex;
    div {
        display: flex;
        align-items: center;
    }
}
/* MAIN CONTENT SERVICES */

/* MAIN CONTENT TEAMS */
.mCteamBot {
    width: 100%;
    flex: 1;
    display: flex;
}
.lines {
    background: #f8f8f8;
}
.lines1 {opacity: .4;}
.lines2 {opacity: .6;}
.lines3 {opacity: .8;}

.mCcontactSBL {
    display: flex;
    flex-direction: column;
    width: 100%;
    flex: 1;
}
.mctbImg {
    img {
        transition: .3s;
    }
    &:hover {
        img {
            filter: grayscale(0%) !important;
        }
    }
}
.mccstSoc {
    display: flex;
}
.divider {
    background: #f8f8f8;
    width: 100%;
}
/* MAIN CONTENT TEAMS */

/* MAIN CONTENT CONTACTS */
.mccstIco {
    display: flex;
    align-items: center;
}
.mccstLogo {
    width: 100%;
}
/* MAIN CONTENT CONTACTS */

